* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    font-family: 'Segoe UI', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    background-color: #FEFEFE;
}

main {
    display: flex;    
    justify-content: space-between;
    align-content: center;
    max-width: 80%;
    margin: auto;
    margin-bottom: 5em;   
}

main > div {
    flex: 1 1 0px;
    align-self: center;
    justify-self: center;
    width: 100%;
    padding: 0 16px;
}

input[type="text"], select {
    display: block;
    background-image:none;
    background-color:transparent;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
}

button {
    display: block;
    background-image: none;
    background-color: none;
    border: none;
    border-radius: none;
}

a {
    color: inherit;
    text-decoration: none;
}

h1 {
    margin-bottom: 18px;
    font-size: 26px;
    color: #2D2D2D;
}

.center {
    width: fit-content;
    padding: 0 16px;
    padding-top: 30px;
    margin: auto;
}

.alert {
    padding: 16px;
    margin: auto;
    font-size: 26px;
}